.wrapper {
  height: 100svh;
  width: 100svw;

  background-color: black;
  font-size: var(--text-size);
}

.switchButton {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px 20px;
  z-index: 40;
}

.textBox {
  z-index: 50;
  position: fixed;
  bottom: 0;

  margin: 2rem;
  padding: 1rem;

  min-height: 25%;
  max-height: 60%;
  width: calc(100% - 4rem);

  background: rgba(0, 0, 0, 0.5);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(11.4px);
  -webkit-backdrop-filter: blur(11.4px);
  border: 1px solid rgba(0, 0, 0, 0.15);

  user-select: none;
}

.textBox > p {
  padding-top: 2rem;

  font-size: 2.5em;
  margin: 0;
  word-wrap: break-word;
  text-align: center;
}

.closed {
  opacity: 0 !important;
  transform: scale(0.5) !important;
}

.question {
  z-index: 99;
  position: absolute;
  top: 0.75rem;
  left: 50%;

  width: 18rem;
  aspect-ratio: 15/18;

  opacity: 1;
  transform: scale(1);
  translate: -50% calc(-100% - 1rem);

  transition: 0.5s;
}

.question > img {
  display: block;

  width: 18rem;
  aspect-ratio: 15/18;
}

.check {
  animation: checked 0.5s;
}
@keyframes checked {
  0% {
    background: rgba(0, 0, 0, 0.5);
  }
  50% {
    background: rgba(0, 255, 0, 0.5);
  }
  100% {
    background: rgba(0, 0, 0, 0.5);
  }
}
