.toggle {
  z-index: 60;
  position: fixed;
  left: 2rem;
  top: 2rem;

  height: 3rem;
  width: 3rem;
  transition: 0.5s ease;
}

.toggle > span {
  height: 100%;
  position: absolute;

  top: 50%;

  height: 0.2rem;
  width: 100%;

  border-radius: 100vh;
  background: white;

  transform: translateY(-50%);
  transition: 0.5s ease;
}

.toggle > span:first-child {
  transform: translateY(-1rem);
}
.toggle > span:last-child {
  transform: translateY(1rem);
}

.open > span:first-child {
  transform: translateY(0);
  rotate: 45deg;
}
.open > span:nth-child(2) {
  opacity: 0;
}
.open > span:last-child {
  transform: translateY(0);
  rotate: -45deg;
}

.open {
  transform: translateX(calc(100svw - 7rem));
}
